.App{
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.App-logo{
	width: 10rem;
}

.flyout {
	display:flex;
	flex-direction: column;
	min-height:100vh;
	justify-content: space-between;
}

.home-feature-box .fa {
	font-size:6rem;
}

.home-feature-box span {
	display: block;
	color:#111;
	font-weight:bold;
	margin-top:1.5rem;
}

.example-components-list li > a {
  color: #495057;
}

.example-components-list li:last-child > a {
	border-bottom:0;
}

.example-components-list li > a .fa {
	color:rgba(0,0,0,.35);
	float:right;
}

.alert{
    position: absolute;
    z-index: 2;
    right: 0;
  }
  
  p{
    margin: 0
  }
.city {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 40px 8%;
    border-radius: 20px;
    background: rgba(250, 250, 250, 0.85);
    box-shadow: 0px 0px 1px 0px rgba(15, 15, 15, 0.404);
}

p {
    margin-top: 10px;
    /* text-transform: uppercase; */
    letter-spacing: 0.05em;
}

.city-temp {
    font-size: 5rem;
    font-weight: bold;
    margin-top: 10px;
    color: #1e2432;
    text-align: center;
}

.city sup {
    font-size: 0.5em;
}

.city-name {
    font-size: 2em;
}

.city-name sup {
    padding: 0.2em 0.6em;
    margin-left: 0.2em;
    border-radius: 30px;
    color: #fff;
    background: #ff8c00;
}

.city-icon {
    margin-top: 10px;
    width: 100px;
    height: 100px;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media only screen and (max-width: 600px) {
    .search {
        padding: 20px 15%;
    }

    .city {
        padding: 40px 20%;
    }
}

.Card_container__Geu50 {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;
  }
  
  .Card_card__ZgN71 {
    margin: 0 2% !important;
  }
  
  .Card_infected__Ag1JP {
    border-bottom: 10px solid rgba(10, 10, 206, 0.5);
  }
  
  .Card_recovered__2SVsF{
    border-bottom: 10px solid rgba(21, 184, 21, 0.582);
  }
  
  .Card_deaths__2FN-l{
    border-bottom: 10px solid rgba(146, 10, 10, 0.767);
  }
  
  @media only screen and (max-width: 770px) {
    .Card_card__ZgN71 {
      margin: 2% 0 !important;
    }
  }
  
body {
    background-color: rgb(250, 250,250);
  }
  
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .image {
    width: 100vw !important;
    height: 10vh !important;
    margin-top: 50px !important;
    justify-content: center !important;
  }
  
  @media only screen and (max-width: 770px) {
    .container {
      margin: 0 10%;
    }
  
    .image {
      width: 100%;
    }
  }
